import React from 'react';
import dayjs from 'dayjs';
/**
 * Date and Time Picker Component
 */
export const DateTimePicker = ({ value, onChange }) => {
    const current = dayjs(value);
    const handleDateChange = (e) => {
        const newDate = dayjs(e.target.value)
            .hour(current.hour())
            .minute(current.minute())
            .toDate();
        onChange(newDate);
    };
    const handleTimeChange = (e) => {
        const [hours, minutes] = e.target.value.split(':').map(Number);
        const newDate = current.hour(hours).minute(minutes).toDate();
        onChange(newDate);
    };
    return (React.createElement("div", null,
        React.createElement("label", { className: "block text-sm font-medium text-newTextColor mb-3" }, "Schedule"),
        React.createElement("div", { className: "grid grid-cols-2 gap-3" },
            React.createElement("div", null,
                React.createElement("label", { className: "block text-xs text-textItemBlur mb-1" }, "Date"),
                React.createElement("input", { type: "date", value: current.format('YYYY-MM-DD'), onChange: handleDateChange, min: dayjs().format('YYYY-MM-DD'), className: "w-full px-3 py-2 bg-newBgColor border border-newBorder rounded text-newTextColor focus:border-btnPrimary focus:outline-none" })),
            React.createElement("div", null,
                React.createElement("label", { className: "block text-xs text-textItemBlur mb-1" }, "Time"),
                React.createElement("input", { type: "time", value: current.format('HH:mm'), onChange: handleTimeChange, className: "w-full px-3 py-2 bg-newBgColor border border-newBorder rounded text-newTextColor focus:border-btnPrimary focus:outline-none" }))),
        React.createElement("div", { className: "mt-2 text-xs text-textItemBlur" },
            "Posting on: ",
            React.createElement("span", { className: "text-newTextColor font-medium" }, current.format('MMMM D, YYYY [at] HH:mm')))));
};
