import React, { useState, useEffect } from 'react';
import { XTwitterSettingsComponent } from './XTwitterSettings';
import { FacebookSettingsComponent } from './FacebookSettings';
import { LinkedInSettingsComponent } from './LinkedInSettings';
import { InstagramSettingsComponent } from './InstagramSettings';
/**
 * Map integration provider to platform type
 */
const getPlatformType = (provider) => {
    const providerLower = provider.toLowerCase();
    if (providerLower.includes('twitter') || providerLower === 'x') {
        return 'x';
    }
    if (providerLower.includes('facebook')) {
        return 'facebook';
    }
    if (providerLower.includes('linkedin')) {
        return 'linkedin';
    }
    if (providerLower.includes('instagram')) {
        return 'instagram';
    }
    if (providerLower.includes('threads')) {
        return 'threads';
    }
    if (providerLower.includes('tiktok')) {
        return 'tiktok';
    }
    if (providerLower.includes('pinterest')) {
        return 'pinterest';
    }
    if (providerLower.includes('youtube')) {
        return 'youtube';
    }
    return null;
};
/**
 * Platform Settings Wrapper Component
 * Conditionally renders platform-specific settings
 */
export const PlatformSettings = ({ integration, onChange }) => {
    const platformType = getPlatformType(integration.identifier);
    // Initialize settings with default values based on platform
    const [settings, setSettings] = useState(() => {
        switch (platformType) {
            case 'x':
                return { __type: 'x', who_can_reply_post: 'everyone' };
            case 'facebook':
                return { __type: 'facebook', privacy: 'public' };
            case 'linkedin':
                return { __type: 'linkedin', visibility: 'public' };
            case 'instagram':
                return { __type: 'instagram', post_type: 'post' };
            case 'threads':
                return { __type: 'threads', who_can_reply: 'everyone' };
            case 'tiktok':
                return { __type: 'tiktok', privacy: 'public' };
            case 'pinterest':
                return { __type: 'pinterest' };
            case 'youtube':
                return { __type: 'youtube', title: '', privacy: 'public' };
            default:
                return { __type: 'x' }; // Fallback
        }
    });
    // Notify parent when settings change
    useEffect(() => {
        onChange(settings);
    }, [settings, onChange]);
    const handleSettingsChange = (newSettings) => {
        setSettings(newSettings);
    };
    // If platform doesn't support custom settings, show nothing
    if (!platformType) {
        return null;
    }
    return (React.createElement("div", { className: "bg-newBgColorInner rounded-lg border border-newBorder p-4" },
        platformType === 'x' && settings.__type === 'x' && (React.createElement(XTwitterSettingsComponent, { settings: settings, onChange: handleSettingsChange })),
        platformType === 'facebook' && settings.__type === 'facebook' && (React.createElement(FacebookSettingsComponent, { settings: settings, onChange: handleSettingsChange })),
        platformType === 'linkedin' && settings.__type === 'linkedin' && (React.createElement(LinkedInSettingsComponent, { settings: settings, onChange: handleSettingsChange })),
        platformType === 'instagram' && settings.__type === 'instagram' && (React.createElement(InstagramSettingsComponent, { settings: settings, onChange: handleSettingsChange })),
        (platformType === 'threads' || platformType === 'tiktok' || platformType === 'pinterest' || platformType === 'youtube') && (React.createElement("div", { className: "text-sm text-textItemBlur text-center py-4" },
            "Advanced settings for ",
            integration.identifier,
            " coming soon"))));
};
