import React from 'react';
import dayjs from 'dayjs';
import isoWeek from 'dayjs/plugin/isoWeek';
import { useCalendarStore, useCalendarContext } from '../context';
import { CalendarSlot } from '../components/CalendarSlot';
dayjs.extend(isoWeek);
const HOURS = Array.from({ length: 24 }, (_, i) => i);
export const WeekView = ({ onCreate, onEdit }) => {
    const { currentDate } = useCalendarStore();
    const { posts, deletePost, reschedulePost } = useCalendarContext();
    // Get week days (Monday - Sunday)
    const weekStart = dayjs(currentDate).startOf('isoWeek');
    const weekDays = Array.from({ length: 7 }, (_, i) => weekStart.add(i, 'day'));
    // Group posts by day and hour
    const getPostsForSlot = (day, hour) => {
        return posts.filter((post) => {
            const postDate = dayjs(post.publishDate);
            return postDate.isSame(day, 'day') && postDate.hour() === hour;
        });
    };
    const handleDrop = async (post, newDate) => {
        await reschedulePost(post.id, newDate.toISOString());
    };
    const handleDelete = async (postId) => {
        await deletePost(postId);
    };
    return (React.createElement("div", { className: "h-full overflow-auto" },
        React.createElement("div", { className: "grid gap-0", style: { gridTemplateColumns: '80px repeat(7, minmax(0, 1fr))' } },
            React.createElement("div", { className: "sticky top-0 bg-newBgColor border-b border-newBorder p-2 z-10" }),
            weekDays.map((day) => (React.createElement("div", { key: day.format('YYYY-MM-DD'), className: "sticky top-0 bg-newBgColor border-b border-l border-newBorder p-3 text-center z-10" },
                React.createElement("div", { className: "text-textItemBlur text-xs uppercase" }, day.format('ddd')),
                React.createElement("div", { className: `text-lg font-semibold mt-1 ${day.isSame(dayjs(), 'day') ? 'text-btnPrimary' : 'text-newTextColor'}` }, day.format('D'))))),
            HOURS.map((hour) => (React.createElement(React.Fragment, { key: hour },
                React.createElement("div", { className: "border-r border-newBorder p-2 text-right text-textItemBlur text-sm" }, dayjs().hour(hour).format('HH:00')),
                weekDays.map((day) => {
                    const slotPosts = getPostsForSlot(day, hour);
                    return (React.createElement(CalendarSlot, { key: `${day.format('YYYY-MM-DD')}-${hour}`, date: day, hour: hour, posts: slotPosts, onDrop: handleDrop, onCreate: onCreate, onDeletePost: handleDelete, onEditPost: onEdit, variant: "week", className: "border-l border-b min-h-[60px] p-1" }));
                })))))));
};
